<template>
     <div class="public_content">
        <div class="public_title"><span>退出门店</span></div>

        <div class="public_tip orange"><i class="iconfont">&#xe66d;</i><span>提示：添加字段添加添加添加添加</span></div>

        <v-code ref="phone" label='手机号：'></v-code>
        
        <el-button type="primary" round @click="onSubmit" class="public_submit">确认退出</el-button>
     </div> 
</template>

<script>
import { agentSignOut } from "@/api/login";
import vCode from "@/components/code/index";
export default {
  data() {
    return {};
  },
  components: {
    vCode
  },
  computed: {},
  methods: {
    onSubmit() {
      if (this.$refs.phone.form.phone && this.$refs.phone.form.code) {
        let data = {
          phone: this.$refs.phone.form.phone,
          code: this.$refs.phone.form.code
        };
        agentSignOut(data).then(res => {
          if (res.data.code == 1) { 
            this.$router.push({name: "home"})
          }
        });
      } else {
        this.$message.warning("信息填写不正确");
      }
    }
  }
};
</script>

<style lang='scss' scoped>
.public_tip {
  margin: 30px 0;
}
</style>



